<template>
  <main class="home">
    <div class="box">
      <h4>Here are the client you have</h4>
      <ApolloQuery
        :query="require('../../graphql/businessClients.gql')"
      >
        <template v-slot="{ result: { loading, error, data } }">
          <!-- Loading -->
          <div v-if="loading" class="loading apollo">Loading...</div>

          <!-- Error -->
          <div v-else-if="error" class="error apollo">An error occurred</div>

          <!-- Result -->
          <div v-else-if="data" class="result apollo">
            <div v-for="client in data.businessClients" :key="client.id">
              <router-link
                :to="{ name: 'ClientProfile', params: { clientID: client.id, clientName: client.name } }"
              >
                <div class="client-item">
                  <div class="icon">{{client.name[0].toUpperCase()}}</div>
                  <div class="info">
                     {{ client.name }}
                  </div>
                </div>
              </router-link>
            </div>
          </div>

          <!-- No result -->
          <div v-else class="no-result apollo">No result :(</div>
        </template>
      </ApolloQuery>
    </div>
  </main>
</template>


<script>
export default {
  name: "ManageClient",
  props: {
    msg: String,
  },
  data() {
    return { 
      
    };
  },
};
</script>

