  border-radius: 5px;
}

.icon {
  width: 40px;
  height: 40px;
  background-color: yellow;
  color: white;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  font-weight: bolder;
}

.info {
  margin: 0 10px;
}
</style>
