<template>
  <main>
    <h1 class="title">Let's create a client</h1>
    <ApolloMutation
      :mutation="require('../../graphql/createClient.gql')"
      :variables="{
        clientNumber,
        name,
        cell,
        email,
        bank,
        bankAccName,
        bankAccType,
        bankAccNumber : bankAccNumber,
        biCode,
      }"
      @done="onDone"
    >
      <template v-slot="{ mutate, loading, error }">
        <form v-on:submit.prevent="mutate()">
          <div class="section-border-one">
          <section>
            <h3 class="title-div">Client Details</h3>
            
            <br>
            <label>Client Number:</label>
            <input type="text" v-model="clientNumber" required />

            <label>Name:</label>
            <input type="text" v-model="name" required />

            <label>Cellphone:</label>
            <input type="tel" v-model="cell" required />

            <label>Email</label>
            <input type="email" v-model="email" required />
          </section>
          </div>

          <div class="section-border-two">
          <section class="section-b">
          <h3 class="title-div-two">Payment Information</h3>
            <label>Account Type:</label>
            <select v-model="bankAccType" required>
              <option value="CHECQUE">Cheque Account</option>
              <option value="SAVINGS">Savings Account</option>
              <option value="TRANSMISSION">Transmission Account</option>
            </select>

            <label>Bank:</label>
            <select v-model="bank" required>
              <option value="1">Bank of Namibia</option>
              <option value="2">Bank Windhoek</option>
              <option value="3">First National Bank</option>
              <option value="4">Nedbank Namibia</option>
              <option value="8">Standard Bank</option>
              <option value="5">Letshego</option>
              <option value="6">BankBIC</option>
              <option value="7">Trustco</option>
              <option value="9">Bank Atlantico</option>
            </select>

            <label>Bank Account Name:</label>
            <input type="text" v-model="bankAccName" required />

            <label>Bank Account Number:</label>
            <input type="text" v-model="bankAccNumber" required pattern="[0-9]+" />

            <label>Bic Code:</label>
            <select v-model="biCode" required>
              <option value="1">CBKNNANX</option>
              <option value="2">BWLINANX</option>
              <option value="3">FIRNNANX</option>
              <option value="4">NEDSNANX</option>
              <option value="8">SBNMNANX</option>
              <option value="5">LFSGNANA</option>
              <option value="6">BBNLNANA</option>
              <option value="7">TRNANANX</option>
              <option value="9">BAPANANX</option>
            </select>

            <input type="submit" value="Continue" :disabled="loading" style="width: 100%; margin-top: 30px; margin-left: 30px;" />

            <p v-if="error">An error occurred: {{ error }}</p>
          </section>
          </div>
        </form>
      </template>
    </ApolloMutation>
  </main>
</template>


<script>
export default {
  name: "CreateClient",
  props: {
    msg: String,
  },
  data() {
    return {
      clientNumber: "",
      name: "",
      cell: "",
      email: "",
      bank: "",
      bankAccName: "",
      bankAccType: "",
      bankAccNumber: "",
      biCode: "",
    };
  },
  methods: {
    onDone() {
      alert("Client created successfully!");
      this.clientNumber = ""
      this.name = ""
      this.cell = ""
      this.email = ""
      this.bank = ""
      this.bankAccName = ""
      this.bankAccType = ""
      this.bankAccNumber = ""
      this.biCode = ""

    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  text-align: left;
  width: 100%;
  background-color: rgb(216, 193, 240);
}

form {
  display: flex;
  margin-top: 15px;
  margin-left: 40px;
  margin-right: 70px;
  justify-content: space-between;
}

.title {
  margin-left: 15px;
  text-align: center;
}

.title-div {
  margin-left: 15px;
  margin-bottom: 2px;
  text-align: center;
}

.title-div-two {
  margin-top: -50px;
  margin-left: 15px;
  margin-bottom: 23px;
  text-align: center;
}

.section-border-one {
  border-radius: 10px;
  display: inline-block;
  color: black;
  background-color: white;
  margin-top: 10px;
  margin-left: 50px;
  width: 400px;
  height: 400px;
  padding: 10px 18px;
}

.section-border-two {
  border-radius: 10px;
  display: inline-block;
  color: black;
  background-color: white;
  margin-top: 10px;
  margin-left: 50px;
  width: 400px;
  height: 475px;
  padding: 10px 18px;
}

.section-b {
  margin-top: 70px;
  margin-right: 50px;
}
</style>
