<template>
  <main>
    <div class="info">
      <div class="box">
        <h5 class="header">{{ clientName }}</h5>
        <section>
          <div class="summary">
            <router-link :to="{ name: 'Contract', params: { clientID } }">
              <button>add</button>
            </router-link>
          </div>

          <template>
            <ApolloQuery
              :query="require('../../graphql/getContract.gql')"
              :variables="{ clientID }"
            >
              <template v-slot="{ result: { loading, error, data } }">
                <!-- Loading -->
                <div v-if="loading" class="loading apollo">Loading...</div>

                <!-- Error -->
                <div v-else-if="error" class="error apollo">
                  An error occurred
                </div>

                <!-- Result -->
                <div v-else-if="data" class="result apollo">
                  <div
                    class="contract-item"
                    v-for="contract in data.getContract"
                    :key="contract.contractID"
                  >
                    <router-link
                      :to="{
                        name: 'ClientInformation',
                        params: { 
                          ...contract
                        },
                      }"
                      >
                      <div class="item-details">
                        <span>CN{{contract.contractID}}</span>
                        <span>{{contract.paymentMethod}}</span>
                        <span>{{contract.installmentDates}}</span>
                        <span>{{contract.installmentAmount}}</span>
                      </div>
                      </router-link
                    >
                  </div>
                </div>

                <!-- No result -->
                <div v-else class="no-result apollo">No result :(</div>
              </template>
            </ApolloQuery>
          </template>
        </section>
      </div>
    </div>
    <div style="flex-grow: 1">
      <router-view></router-view>
    </div>
  </main>
</template>


<script>
export default {
  name: "ClientProfile",
  props: {
    clientID: String,
    clientName: String,
  },
  data() {
    return {};
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  display: flex;
  flex-direction: row;
  max-height: 100vh;
  overflow: hidden;
  flex-grow: 1;
}

.info {
  width: 400px;
  min-width: 400px;
  background-color: plum;
  max-height: 100vh;
  overflow-y: scroll;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.box {
  margin: 15px;
  position: relative;
}

.contract-item {
  padding: 10px 10px;
  background-color: rgb(104, 14, 104);
  margin: 10px 0;
  text-align: left;
  border-radius: 5px;
}

section {
  padding-top: 70px;
}

button {
  padding: 7px 15px;
  border-radius: 5px;
  border: none;
}

.summary {
  text-align: right;  
}

.item-details  {
  display: flex;
  justify-content: space-around;
}
span {
  margin: 0 5px
}
</style>
