<template>
  <main>
    <div :class="['form-box', { active }]">
      <h2>Sign In</h2>
      <form v-on:submit.prevent="signinHandler">
        <label>Username:</label>
        <input type="text" v-model="username" required />

        <label>Password:</label>
        <input type="password" v-model="password" required />

        <input
          :disabled="loading"
          type="submit"
          value="Continue"
          class="submit-btn"
        />

        <p v-if="signinError">{{ signinError }}</p>
      </form>
      <div class="click-text" @click="active = true">I want to Sign Up</div>
    </div>
    <div
      :class="['form-box', { active }]"
      style="background-color: #4a148c; color: white"
    >
      <h2>Sign Up</h2>
      <form v-on:submit.prevent="signupHandler">
        <label>Username:</label>
        <input type="text" required v-model="rUsername" />

        <label>Email:</label>
        <input type="text" required v-model="rEmail" />

        <label>Password:</label>
        <input type="password" required v-model="rPassword" />

        <label>Re-Type Password:</label>
        <input type="password" required v-model="cPassword" />

        <input
          :disabled="loading"
          type="submit"
          value="Continue"
          class="submit-btn"
        />
        <p v-if="error">An error occurred: {{ error }}</p>
      </form>
      <div class="click-text" @click="active = false">I want to Sign In</div>
    </div>
  </main>
</template>


<script>
import { onLogin } from "../vue-apollo";

export default {
  name: "SignForm",
  props: {
    msg: String,
  },
  data() {
    return {
      active: false,
      username: "",
      password: "",
      rUsername: "",
      rPassword: "",
      cPassword: "",
      rEmail: "",
      signinError: "",
      signupError: "",
    };
  },
  methods: {
    signinHandler() {
      localStorage.clear()
      this.$apollo
        .mutate({
          // Query
          mutation: require("../graphql/signin.gql"),
          // Parameters
          variables: {
            username: this.username,
            password: this.password,
          },
        })
        .then((data) => {
          // Result
          data = data.data.login;

          onLogin(this.$apolloProvider.defaultClient, data.token);
          if (data.businessName) {
            localStorage.setItem("bizname", data.businessName);
            this.$router.push("/launchpad");
          } else {
            this.$router.push("/register");
          }
        })
        .catch((error) => {
          // Error
          this.signinError = "Invalid login";
          console.log(error);
        });
    },
    signupHandler() {
      localStorage.clear()
      this.$apollo
        .mutate({
          // Query
          mutation: require("../graphql/signup.gql"),
          // Parameters
          variables: {
            username: this.rUsername,
            password: this.rPassword,
            email: this.rEmail,
          },
        })
        .then((data) => {
          // Result
          onLogin(this.$apolloProvider.defaultClient, data.data.register);
          this.$router.push("register");

          // console.log(data);
        })
        .catch((error) => {
          // Error
          this.signupError = "Invalid login";
          console.error(error);
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  margin: 40px;
  background-color: none;
  color: black;
  min-width: 370px;
  max-width: 370px;
  height: 500px;
  overflow: hidden;
  border-radius: 10px;
  border: 3px solid;
}

.form-box {
  min-width: 350px;
  height: 100%;
  display: inline;
  position: relative;
  left: 0;
  transition: 0.3s ease;
}

form {
  text-align: left;
  width: 175px;
  margin: 0 auto;
}

.active {
  left: -330px;
}


.submit-btn {
  width: 100%;
  background-color: #FFC107;
}

input {
  max-width: 175px; 
}

</style>
